import * as React from "react";
import { FC, memo, useEffect, useState } from "react";

import classNames from "classnames";

import styles from "./Select.module.scss";

type SelectOption = {
  id: string;
  value: string;
};

type SelectProps = {
  options: SelectOption[];
  placeholder?: string;
  onSelectOption: (item: SelectOption) => void;
  className?: string;
} & React.HTMLAttributes<HTMLDivElement>;

const Select: FC<SelectProps> = ({
  options,
  onSelectOption,
  className = "",
  placeholder = "",
}) => {
  // State
  const [opened, setOpened] = useState<boolean>(false);
  const [value, setValue] = useState<SelectOption>();

  // Handlers
  const handleOnClick = () => setOpened(!opened);
  const handleSelectOption = (e: React.MouseEvent<HTMLDivElement>) => {
    setValue({
      id: (e.target as HTMLDivElement).dataset["id"] || "",
      value: (e.target as HTMLDivElement).dataset["value"] || "",
    });
  };

  useEffect(() => {
    if (typeof value !== "undefined") {
      onSelectOption(value);
    }
  }, [value]);

  return (
    <div
      className={classNames(styles.select, className)}
      onClick={handleOnClick}
    >
      <input
        className={classNames(
          styles.select__input,
          opened ? styles.select__input_open : styles.select__input_close
        )}
        placeholder={placeholder}
        disabled
      />
      <div
        className={classNames(
          styles.select__list,
          opened ? styles.select__list_open : styles.select__list_close
        )}
      >
        {options.map((item) => (
          <div
            key={item.id}
            data-id={item.id}
            data-value={item.value}
            className={classNames(styles.select__option)}
            onClick={handleSelectOption}
          >
            {item.value}
          </div>
        ))}
      </div>
    </div>
  );
};

export type { SelectOption, SelectProps };
export default memo(Select);
