import CoingeckoStore from "@store/CoinGeckoStore";
import { CoinApi, CoinModel, normalizeCoin } from "@store/models/CoinGecko";
import { normalizeCollection } from "@store/models/shared/collection";
import rootStore from "@store/RootStore";
import QueryParamsStore from "@store/RootStore/QueryParamsStore";
import { Currency } from "@utils/Currency";
import log from "@utils/log";
import { QueryParam } from "@utils/QueryParam";
import { SortType } from "@utils/SortType";
import {
  action,
  autorun,
  computed,
  IReactionDisposer,
  makeObservable,
  observable,
  reaction,
} from "mobx";

type PrivateFields = "_list";

interface SearchCoinRecord {
  [key: string]: CoinApi[];
}

type CoinApiType = CoinApi[] | SearchCoinRecord;

/**
 * Provides access to coins information
 */
class CoinStore extends CoingeckoStore<CoinApiType, CoinModel> {
  private _list: CoinModel[] = [];
  private _sortedList: CoinModel[] = [];
  private _sort: string = "";

  constructor() {
    super();
    makeObservable<CoinStore, PrivateFields>(this, {
      _list: observable.ref,
      list: computed,
      vsCurrency: computed,
      setList: action,
      sortData: action,
    });
  }

  get vsCurrency() {
    return (
      rootStore.query.getStringifiedParam(QueryParam.CURRENCY) || Currency.USD
    );
  }

  get list(): CoinModel[] {
    return this._list;
  }

  setList(val: CoinModel[]) {
    this._list = val;
  }

  get coinsCount(): number {
    return this.data.length;
  }

  private async sortGainDesc() {
    this._sortedList = [...this.data];
    this._sortedList.sort(
      (f, s) => s.priceChangePercentage24h - f.priceChangePercentage24h
    );
  }

  private async sortGainAsc() {
    this._sortedList = [...this.data];
    this._sortedList.sort(
      (f, s) => f.priceChangePercentage24h - s.priceChangePercentage24h
    );
  }

  get isListEnd() {
    if (this._sort !== SortType.FAV) {
      return this.list.length === 0;
    }
    return false;
  }

  private async sortFav(query: QueryParamsStore) {
    const fa = query.getStringifiedParam(QueryParam.FAV).split(",");
    const aux: CoinModel[] = [];
    for (const f of fa) {
      const el = this.data.find((coin: CoinModel) => coin.id === f);
      if (typeof el !== "undefined") {
        aux.push(el);
      }
    }
    this._sortedList = aux;
  }

  private sorting(query: QueryParamsStore) {
    switch (this._sort) {
      case SortType.MARKET_DESC:
        this._sortedList = [...this.data];
        break;
      case SortType.GAIN_DESC:
        this.sortGainDesc();
        break;
      case SortType.GAIN_ASC:
        this.sortGainAsc();
        break;
      case SortType.FAV:
        this.sortFav(query);
        break;
    }
  }

  sortData(query: QueryParamsStore) {
    const s = query.getStringifiedParam(QueryParam.SORT);
    if (s !== "") {
      if (this._sort !== s) {
        this._sort = s;
      }
      this.sorting(query);
    }
  }

  normalizeApiData(d: CoinApiType): void {
    if (Array.isArray(d)) {
      this.setData(
        normalizeCollection(
          d.map((item) => normalizeCoin(item)),
          (el) => el.id
        )
      );
    } else {
      this.setData(
        normalizeCollection(
          d["coins"].map((item) => normalizeCoin(item)),
          (el) => el.id
        )
      );
    }
    this.setList(this.data);
    this.reactOnQs(rootStore.query);
  }

  getCoins() {
    autorun(() => {
      let cur =
        rootStore.query.getStringifiedParam(QueryParam.CURRENCY) ||
        Currency.USD;
      const c = rootStore.query.getStringifiedParam(QueryParam.CATEGORY);
      const n = rootStore.query.getStringifiedParam(QueryParam.NAME);
      const p = rootStore.query.getStringifiedParam(QueryParam.PAGE);
      if (c) {
        this.getCoinsByCurrencyCategory(cur, c.toString());
      } else if (n) {
        this.searchCoins(n.toString());
      } else if (p) {
        this.getCoinsByCurrency(cur);
      }
    });
  }

  async getCoinsByCurrency(currency: string): Promise<void> {
    this.getDataFromApiStore(
      `/coins/markets?vs_currency=${currency}&sparkline=true`
    );
  }

  async searchCoins(name: string): Promise<void> {
    this.getDataFromApiStore(`/search?query=${name}`);
  }

  async getCoinsByCurrencyId(currency: string, id: string): Promise<void> {
    this.getDataFromApiStore(
      `/coins/markets?vs_currency=${currency}&ids=${id}&sparkline=true`
    );
  }

  async getCoinsByCurrencyCategory(
    currency: string,
    category: string
  ): Promise<void> {
    this.getDataFromApiStore(
      `/coins/markets?vs_currency=${currency}&category=${category}&sparkline=true`
    );
  }

  async reactOnParamsChange(): Promise<void> {
    autorun(() => {
      let cur =
        rootStore.query.getStringifiedParam(QueryParam.CURRENCY) ||
        Currency.USD;
      const c = rootStore.query.getParam(QueryParam.CATEGORY);
      if (c !== null && typeof c !== "undefined" && c !== "") {
        this.getCoinsByCurrencyCategory(cur, c.toString());
      }
    });
  }

  filterByPage(query: QueryParamsStore) {
    const page = query.getParam("page");
    const perPage = query.getParam("per_page");
    if (typeof page !== "undefined" && typeof perPage !== "undefined") {
      const p = parseInt(`${page}`);
      const pp = parseInt(`${perPage}`);
      this.setList(this._sortedList.slice(0, pp * p));
    }
  }

  private reactOnQs(query: QueryParamsStore) {
    this.sortData(query);
    this.filterByPage(query);
  }

  destroy(): void {}
}

export default CoinStore;
