import React, { FC, memo, useEffect, useState } from "react";

import log from "@utils/log";
import { QueryParam } from "@utils/QueryParam";
import { SortType } from "@utils/SortType";
import classNames from "classnames";
import { useSearchParams } from "react-router-dom";

import TabCoinsOrderItem from "./components/TabCoinsOrderItem";
import { Tabs } from "./config/Tabs";
import styles from "./TabCoinsOrder.module.scss";

export type Tab = {
  id: string;
  sort: SortType;
};

export type TabsCoinsOrderProps = {
  className?: string;
};

const TabsCoinsOrder: FC<TabsCoinsOrderProps> = ({ className }) => {
  // State
  const [searchParams, setSearchParams] = useSearchParams();
  const [activeId, setActiveId] = useState("");

  // Lifecycle
  useEffect(() => {
    const sp = searchParams.get(QueryParam.SORT);
    setActiveId(`${sp}`);
  }, [searchParams]);

  // Render
  return (
    <div className={classNames(className, styles["tab-coins-order"])}>
      {Tabs.map((t) => (
        <TabCoinsOrderItem
          className={styles["tab-coins-order__item"]}
          key={t.id}
          id={t.id}
          sort={t.sort}
          active={t.sort === activeId}
        />
      ))}
    </div>
  );
};

export default memo(TabsCoinsOrder);
