import * as React from "react";

import Text, { TextColor, TextProps, TextWeight } from "@components/Text";
import classNames from "classnames";

import styles from "./CoinPriceChangePer.module.scss";

export type CoinPriceChangePerProps = {
  price: number;
  brackets?: boolean;
} & TextProps;

export const CoinPriceChangePer: React.FC<CoinPriceChangePerProps> = ({
  price,
  className,
  color,
  weight = TextWeight.b,
  brackets = false,
  ...htmlSpanAttrs
}) => {
  return (
    <Text
      color={price > 0 ? TextColor.green : TextColor.red}
      weight={weight}
      className={classNames(styles["coin-price-change-per"], className)}
      {...htmlSpanAttrs}
    >
      {brackets ? `(${price.toFixed(2)}%)` : `${price.toFixed(2)}%`}
    </Text>
  );
};
