import CoingeckoStore from "@store/CoinGeckoStore";
import { GlobalApi, GlobalModel, normalizeGlobal } from "@store/models/CoinGecko";
import { normalizeCollection } from "@store/models/shared/collection";
import log from "@utils/log";
import { action, computed, makeObservable } from "mobx";

class GlobalStore extends CoingeckoStore<GlobalApi, GlobalModel> {
  private _endpoint = "/global";

  constructor() {
    super();

    makeObservable<GlobalStore>(this, {
      marketChange: computed,
      getGlobal: action,
    });
  }

  get marketChange() {
    return parseFloat(this.data[0].marketChange)
  }

  normalizeApiData(d: GlobalApi): void {
    this.setData(normalizeCollection([normalizeGlobal(d)], (item) => "market"))
  }

  destroy(): void {}

  async getGlobal() {
    this.getDataFromApiStore(this._endpoint);
  }
}

export default GlobalStore;