import { ScaleUnit } from "@pages/PageCoinDetails/components/CoinDetailedGraph/components/ControlsScale";
import {
  options,
  TicksFormatterCb,
} from "@pages/PageCoinDetails/components/CoinDetailedGraph/config/options";
import { subMinutesFromCur } from "@pages/PageCoinDetails/components/CoinDetailedGraph/util/time";
import { CoinDetailsGraphChartContext } from "@pages/PageCoinDetails/components/CoinDetailedGraph/util/types";
import CoinGeckoStore from "@store/CoinGeckoStore";
import {
  CoinHistoryApi,
  CoinHistoryModel,
  normalizeCoinHistory,
} from "@store/models/CoinGecko/CoinHistory";
import { normalizeCollection } from "@store/models/shared/collection";
import rootStore from "@store/RootStore";
import { Currency } from "@utils/Currency";
import CurrencySigns from "@utils/CurrencySigns";
import { QueryParam } from "@utils/QueryParam";
import { CoreScaleOptions, Scale } from "chart.js";
import { Context } from "chartjs-plugin-datalabels";
import { action, computed, makeObservable } from "mobx";

class StoreCoinHistory extends CoinGeckoStore<
  CoinHistoryApi,
  CoinHistoryModel
> {
  constructor() {
    super();
    makeObservable<StoreCoinHistory>(this, {
      graphHistoryData: computed,
      vsCurrency: computed,
      days: computed,
      graphScale: computed,
      labels5Min: computed,
      labels1H: computed,
      label1D: computed,
      labelMax: computed,
      labels: computed,
      options: computed,
      getCoinHistory: action,
    });
  }

  get vsCurrency(): string {
    return (
      rootStore.query.getStringifiedParam(QueryParam.CURRENCY) || Currency.USD
    );
  }

  get graphScale(): string {
    return rootStore.query.getStringifiedParam(QueryParam.GRAPH_SCALE);
  }

  get days(): number | string {
    const qp = this.graphScale;
    switch (qp) {
      case "1 H":
      case "24 H":
        return 1;
      case "1 W":
        return 7;
      case "1 M":
        return 31;
      case "6 M":
        return 186;
      case "1 Y":
        return 365;
      case "All":
        return "max";
    }
    return 0;
  }

  get options(): {} {
    const qp = this.graphScale;
    let sc = 0;
    switch (qp) {
      case "24 H":
        sc = 48;
        break;
      case "1 W":
        sc = 3;
        break;
      default:
        sc = 2;
        break;
    }
    const opts = JSON.parse(JSON.stringify(options));
    opts.scales.x.ticks.callback = function (
      this: Scale<CoreScaleOptions>,
      tickValue: string | number,
      index: number
    ) {
      if (typeof tickValue === "number") {
        return index % sc === 0 ? this.getLabelForValue(tickValue) : null;
      }
      return null;
    };
    opts.plugins.datalabels.formatter = (value: number, context: Context) => {
      const c = context as CoinDetailsGraphChartContext;
      const fc = new CurrencySigns();
      return c.dataset.max === value || c.dataset.min === value
        ? `${fc.getCurrencySign(c.dataset.vsCurrency)}${value
            .toFixed(2)
            .toString()
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}`
        : "";
    };
    opts.plugins.datalabels.anchor = (context: Context) => {
      const a = [];
      const c = context as CoinDetailsGraphChartContext;
      const v = c.dataset.data[context.dataIndex];
      if (v === c.dataset.max) {
        a.push("end");
      } else {
        a.push("start");
      }
      return a;
    };
    opts.plugins.datalabels.align = (context: Context) => {
      const a = [];
      const c = context as CoinDetailsGraphChartContext;
      const v = c.dataset.data[context.dataIndex];
      if (v === c.dataset.max) {
        a.push("top");
      } else {
        a.push("bottom");
      }
      return a;
    };
    return opts;
  }

  get labelMax(): string[] {
    return this.graphHistoryData
      .map((_, idx) => {
        const d = subMinutesFromCur(1440 * idx);
        return `${
          d.getMonth() < 10 ? "0" : ""
        }${d.getMonth()}:${d.getFullYear()}`;
      })
      .reverse();
  }

  get label1D(): string[] {
    return this.graphHistoryData
      .map((_, idx) => {
        const d = subMinutesFromCur(1440 * idx);
        return `${d.getDay() < 10 ? "0" : ""}${d.getDay()}:${
          d.getMonth() < 10 ? "0" : ""
        }${d.getMonth()}`;
      })
      .reverse();
  }

  get labels1H(): string[] {
    return this.graphHistoryData
      .map((_, idx) => {
        const d = subMinutesFromCur(60 * idx);
        return `${d.getHours() < 10 ? "0" : ""}${d.getHours()}:${
          d.getMinutes() < 10 ? "0" : ""
        }${d.getMinutes()}`;
      })
      .reverse();
  }

  get labels5Min(): string[] {
    return this.graphHistoryData
      .map((_, idx) => {
        const d = subMinutesFromCur(5 * idx);
        return `${d.getHours() < 10 ? "0" : ""}${d.getHours()}:${
          d.getMinutes() < 10 ? "0" : ""
        }${d.getMinutes()}`;
      })
      .reverse();
  }

  get labels(): string[] {
    const qp = this.graphScale;
    switch (qp) {
      case "1 H":
      case "24 H":
        return this.labels5Min;
      case "1 W":
      case "1 M":
        return this.labels1H;
      case "6 M":
      case "1 Y":
        return this.label1D;
      case "All":
        return this.labelMax;
    }
    return [];
  }

  get graphHistoryData() {
    if (this.data.length !== 0) {
      const d = this.data[0].prices.map((el) => parseFloat(el[1]));
      if (this.graphScale === "1 H") {
        return d.slice(-12, -1);
      }
      return d;
    }
    return [];
  }

  // https://api.coingecko.com/api/v3/coins/bitcoin/market_chart?vs_currency=usd&days=1
  getCoinHistory(coinId: string) {
    this.getDataFromApiStore(
      `coins/${coinId}/market_chart?vs_currency=${this.vsCurrency}&days=${this.days}`
    );
  }

  normalizeApiData(d: CoinHistoryApi): void {
    this.setData(normalizeCollection([normalizeCoinHistory(d)], () => "1"));
  }

  destroy(): void {}
}

export default StoreCoinHistory;
