import * as React from "react";
import { memo } from "react";

import { CoinName } from "@components/CoinName";
import CoinPrice from "@components/CoinPrice";
import { CoinPriceChangePer } from "@components/CoinPriceChangePer";
import { Pic } from "@components/Pic/Pic";
import Symbol from "@components/Symbol";
import { CoinModel } from "@store/models/CoinGecko";
import classNames from "classnames";

import CoinGraph, { LineColor } from "../CoinGraph";
import styles from "./CoinItem.module.scss";

export type CoinItemProps = {
  coin: CoinModel;
  sign?: string;
  onClick: () => {};
};

export const CoinItem: React.FC<CoinItemProps> = memo(
  ({ onClick, coin, sign }) => {
    return (
      <div
        className={classNames(
          styles["coin-item"],
          styles["coin-item_theme_light"]
        )}
        onClick={onClick}
      >
        <div className={styles["coin-item__left-col"]}>
          <Pic
            className={styles["coin-item__pic"]}
            src={coin.imageUrl || coin.large}
            alt=""
          />
          <div className={styles["coin-item__sig"]}>
            <CoinName className={styles["coin-item__name"]} name={coin.name} />
            <Symbol
              className={classNames(
                styles["coin-item__symbol"],
                styles["coin-item__symbol_theme_light"]
              )}
              name={coin?.symbol}
            />
          </div>
        </div>
        <div className={styles["coin-item__wrapper-graph-prices"]}>
          {coin.sparklineIn7d && (
            <CoinGraph
              coin={coin}
              lineColor={
                coin.priceChange24h > 0 ? LineColor.GREEN : LineColor.RED
              }
            />
          )}
          <div className={styles["coin-item__right-col"]}>
            {coin.currentPrice && (
              <CoinPrice
                className={classNames(
                  styles["coin-item__current-price"],
                  styles["coin-item__current-price_theme_light"]
                )}
                price={coin?.currentPrice || 0}
                sign={sign}
              />
            )}
            {coin.priceChange24h && (
              <CoinPriceChangePer
                className={classNames(
                  styles["coin-item__price-change"],
                  styles["coin-item__price-change_theme_light"]
                )}
                price={coin?.priceChange24h || 0}
              />
            )}
          </div>
        </div>
      </div>
    );
  }
);
