import React, { FC, useCallback, useEffect, useState } from "react";

import Select, { SelectOption } from "@components/Select";
import { useLocalStore } from "@hooks/useLocalStore";
import CurrencyStore from "@store/CurrencyStore";
import { QueryParam } from "@utils/QueryParam";
import setQueryParam from "@utils/useQueryParam";
import { observer } from "mobx-react-lite";
import { useSearchParams } from "react-router-dom";

type SelectCurrencyProps = {
  className?: string;
};

const SelectCurrency: FC<SelectCurrencyProps> = ({ className }) => {
  // State
  const [searchParams, setSearchParams] = useSearchParams();
  const [selectedOption, setSelectedOption] = useState("");

  // useCustomHooks
  const currencyStore = useLocalStore(() => new CurrencyStore());

  // Handlers
  const handleChange = useCallback((selectedOption: SelectOption) => {
    if (typeof selectedOption.id !== "undefined") {
      setSelectedOption(selectedOption.id);
    }
  }, []);
  const handleSelectOption = useCallback(
    (option: string) => {
      setQueryParam(searchParams, QueryParam.CURRENCY, option);
      setSearchParams(searchParams);
    },
    [searchParams, setSearchParams]
  );

  // Utils
  const initSearchParam = useCallback(() => {
    const currency = searchParams.get(QueryParam.CURRENCY);
    if (currency !== null) {
      setSelectedOption(currency);
    }
  }, []);

  // LiveCycle
  useEffect(() => {
    initSearchParam();
  }, []);
  useEffect(() => {
    currencyStore.getCurrencies();
  }, [currencyStore]);
  useEffect(() => {
    handleSelectOption(selectedOption);
  }, [selectedOption]);

  // Render
  return (
    <Select
      className={className}
      options={currencyStore.data.map((currency): SelectOption => {
        return { id: currency.id, value: currency.id };
      })}
      onSelectOption={handleChange}
      placeholder={selectedOption === "" ? "market-cur" : selectedOption}
    />
  );
};

export default observer(SelectCurrency);
