import * as React from "react";
import classNames from "classnames";

import styles from "./Pic.module.scss";

export enum PicSize {
  s = "small",
  m = "medium",
}

export type PicProps = {
  size?: PicSize;
} & React.ImgHTMLAttributes<HTMLImageElement>;

export const Pic: React.FC<PicProps> = ({
  alt = "",
  className,
  size = PicSize.m,
  ...htmlImgAttrs
}) => {
  return (
    <img
      className={classNames(styles.pic, styles[`pic_size_${size}`], className)}
      alt={alt}
      {...htmlImgAttrs}
    />
  );
};
