import * as React from "react";
import { FC, useCallback, useEffect, useState } from "react";

import Select, { SelectOption } from "@components/Select";
import { useLocalStore } from "@hooks/useLocalStore";
import CategoryStore from "@store/CategoryStore";
import { CategoryModel } from "@store/models/CoinGecko";
import { QueryParam } from "@utils/QueryParam";
import { observer } from "mobx-react-lite";
import { useSearchParams } from "react-router-dom";

type FilterByCategoryProps = {} & React.HTMLAttributes<HTMLSelectElement>;

/**
 * Filters by categories
 */
const FilterByCategory: FC<FilterByCategoryProps> = () => {
  // State
  const [searchParams, setSearchParams] = useSearchParams();
  const [placeholder, setPlaceholder] = useState("");

  // Hooks
  const categoryStore = useLocalStore(() => new CategoryStore());

  // Handlers
  const handleChange = useCallback(
    (selectedOption: SelectOption) => {
      if (typeof selectedOption.id !== "undefined") {
        searchParams.set(QueryParam.CATEGORY, selectedOption.id);
        setSearchParams(searchParams);
        setPlaceholder(selectedOption.value);
      }
    },
    [searchParams, setSearchParams]
  );

  // Live cycle handle
  useEffect(() => {
    const s = searchParams.get(QueryParam.CATEGORY);
    if (s !== null) {
      setPlaceholder(s);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    categoryStore.getCoinsCategories();
  }, [categoryStore]);

  return (
    <Select
      options={categoryStore.data.map(
        (category: CategoryModel): SelectOption => {
          return { id: category.categoryId, value: category.name };
        }
      )}
      onSelectOption={handleChange}
      placeholder={placeholder === "" ? "Category" : placeholder}
    />
  );
};

export default observer(FilterByCategory);
