import React, { FC, useCallback, useEffect, useState } from "react";

import log from "@utils/log";
import { QueryParam } from "@utils/QueryParam";
import classNames from "classnames";
import { useSearchParams } from "react-router-dom";

import styles from "./CoinFav.module.scss";

type CoinFavProps = {
  coinId: string;
};

const CoinFav: FC<CoinFavProps> = ({ coinId }) => {
  // Use
  const [searchParams, setSearcParams] = useSearchParams();
  const [active, setActive] = useState<boolean>();

  // Handler
  const handleOnClick = () => {
    let f = searchParams.get(QueryParam.FAV);
    if (f === null) {
      searchParams.set(QueryParam.FAV, coinId);
    } else {
      const fs = f.split(",");
      if (fs.length === 1) {
        if (fs[0] === coinId) {
          searchParams.delete(QueryParam.FAV);
        } else if (fs.find((el) => el === coinId) !== "undefined") {
          fs.push(coinId);
          searchParams.set(QueryParam.FAV, fs.join(","));
        } else {
          searchParams.set(
            QueryParam.FAV,
            fs.filter((el) => el !== coinId).join(",")
          );
        }
      } else {
        if (typeof fs.find((el) => el === coinId) === "undefined") {
          fs.push(coinId);
          searchParams.set(QueryParam.FAV, fs.join(","));
        } else {
          searchParams.set(
            QueryParam.FAV,
            fs.filter((el) => el !== coinId).join(",")
          );
        }
      }
    }
    setSearcParams(searchParams);
  };

  // Utils
  const handleActive = useCallback(() => {
    const sp = searchParams.get(QueryParam.FAV);
    if (sp !== null) {
      const fs = sp.split(",");
      if (fs.length === 1) {
        if (fs[0] === coinId) {
          setActive(true);
        } else {
          setActive(false);
        }
      } else {
        if (typeof fs.find((el) => el === coinId) !== "undefined") {
          setActive(true);
        } else {
          setActive(false);
        }
      }
    } else {
      setActive(false);
    }
  }, [coinId, searchParams]);

  // Lifecycle
  useEffect(() => {
    handleActive();
  }, [searchParams, handleActive]);

  // Rander
  return (
    <div
      className={classNames(
        styles["coin-fav"],
        active && styles["coin-fav_active"]
      )}
      onClick={handleOnClick}
    ></div>
  );
};

export default CoinFav;
