import React, { FC, useEffect } from "react";

import Loader from "@components/Loader";
import { useLocalStore } from "@hooks/useLocalStore";
import StoreCoinHistory from "@store/StoreCoinHistory";
import { ResponseState } from "@utils/ResponseState";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip,
} from "chart.js";
import ChartDataLabels from "chartjs-plugin-datalabels";
import zoomPlugin from "chartjs-plugin-zoom";
import classNames from "classnames";
import { observer } from "mobx-react-lite";
import { Line } from "react-chartjs-2";

import styles from "./CoinDetailedGraph.module.scss";
import ControlsScale from "./components/ControlsScale";
import {
  CoinDetailedGraphProps,
  CoinDetailsGraphChartDataset,
} from "./util/types";
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip,
  ChartDataLabels,
  zoomPlugin
);

const CoinDetailedGraph: FC<CoinDetailedGraphProps> = ({
  coin,
  vsCurrency,
  className,
}) => {
  // Storage init
  const coinHistoryStore = useLocalStore(() => new StoreCoinHistory());

  // Utils
  const makeDataset = (hd: number[]): CoinDetailsGraphChartDataset => {
    return {
      max: Math.max(...hd),
      min: Math.min(...hd),
      data: hd,
      borderColor: "#0063F5",
      backgroundColor: "#0063F5",
      vsCurrency,
    };
  };

  // useEffect
  useEffect(() => {
    return () => {};
  }, []);
  useEffect(() => {
    if (typeof coin !== "undefined") {
      coinHistoryStore.getCoinHistory(coin.id);
    }
  }, [coinHistoryStore, coin]);

  // Render
  return (
    <div className={styles["coin-detailed-graph"]}>
      <div
        className={classNames(className, styles["coin-detailed-graph__graph"])}
      >
        {coinHistoryStore.responseState === ResponseState.LOADING ? (
          <Loader />
        ) : (
          <Line
            data={{
              labels: coinHistoryStore.labels,
              datasets: [makeDataset(coinHistoryStore.graphHistoryData)],
            }}
            options={coinHistoryStore.options}
          ></Line>
        )}
        <ControlsScale />
      </div>
    </div>
  );
};

export default observer(CoinDetailedGraph);
