import * as React from "react";
import { useEffect } from "react";

import { CoinPriceChangePer } from "@components/CoinPriceChangePer";
import Text, { TextColor, TextSize, TextWeight } from "@components/Text";
import { useLocalStore } from "@hooks/useLocalStore";
import GlobalStore from "@store/GlobalStore";
import { ResponseState } from "@utils/ResponseState";
import classNames from "classnames";
import { observer } from "mobx-react-lite";

import styles from "./MarketState.module.scss";

export type NarketStateProps = {} & React.HTMLAttributes<HTMLDivElement>;

const MarketState: React.FC<NarketStateProps> = ({ className }) => {
  const globalStore = useLocalStore(() => new GlobalStore());

  useEffect(() => {
    globalStore.getGlobal();
  }, [globalStore]);

  return (
    <div className={classNames(styles["market-state"], className)}>
      <div>
        <Text size={TextSize.ml} weight={TextWeight.b}>
          Market is
        </Text>
        {globalStore.responseState === ResponseState.SUCCESS && (
          <>
            <Text size={TextSize.ml} weight={TextWeight.b}>
              {globalStore.marketChange > 0 ? <> up </> : <> down </>}
            </Text>
            <CoinPriceChangePer price={globalStore.marketChange} />
          </>
        )}
      </div>
      <Text color={TextColor.descend} size={TextSize.es}>
        In the past 24 hour
      </Text>
    </div>
  );
};

export default observer(MarketState);
