import * as React from "react";
import { memo, FC, useCallback } from "react";

import Button, { ButtonProps } from "@components/Button";
import { RoutePath } from "@utils/RoutePath";
import { useNavigate, useSearchParams } from "react-router-dom";

export type ButtonGoPageProps = ButtonProps & {
  page?: RoutePath;
  onBeforeGo: (params: URLSearchParams) => void;
};

export const ButtonGoPage: FC<ButtonGoPageProps> = ({
  className,
  page = RoutePath.Root,
  onBeforeGo,
  icon,
  children,
}) => {
  // useHook
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  // handle
  const handleClick = async () => {
    onBeforeGo(searchParams);
    navigate(`${page}?${searchParams.toString()}`);
  };

  // Render
  return (
    <Button className={className} onClick={handleClick} icon={icon} linked>
      {children}
    </Button>
  );
};

export default memo(ButtonGoPage);
