import * as React from "react";
import { memo } from "react";

import Text, { TextColor, TextProps, TextSize } from "@components/Text";

export type CoinPriceProps = {
  price: number;
  className?: string;
  sign?: string;
} & TextProps;

const CoinPrice: React.FC<CoinPriceProps> = ({
  price,
  className,
  size = TextSize.ms,
  sign = "",
}) => {
  return (
    <Text className={className} color={TextColor.dark} size={size}>
      {`${sign}${price
        .toFixed(2)
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}`}
    </Text>
  );
};

export default memo(CoinPrice);
