import * as React from "react";
import { memo } from "react";

import { CoinName } from "@components/CoinName";
import CoinPrice from "@components/CoinPrice";
import { CoinPriceChange } from "@components/CoinPriceChange";
import { CoinPriceChangePer } from "@components/CoinPriceChangePer";
import { Pic, PicSize } from "@components/Pic";
import Symbol from "@components/Symbol";
import { CoinModel } from "@store/models/CoinGecko";
import CurrencySigns from "@utils/CurrencySigns";

import { Back } from "../../../../components/ButtonGoBack";
import CoinDetailedGraph from "../CoinDetailedGraph";
import CoinFav from "../CoinFav";
import styles from "./CoinDetailsView.module.scss";
import { Transaction } from "./components/Transaction/Transaction";
import { TextColor, TextSize } from "@components/Text";

export type CoinDetailsViewProp = {
  data: CoinModel;
  vsCurrency: string;
  onBeforeGo: (params: URLSearchParams) => void;
};

const CoinDetailsView: React.FC<CoinDetailsViewProp> = ({
  data,
  onBeforeGo,
  vsCurrency,
}) => {
  const fc = new CurrencySigns();
  return (
    <div className={styles["coin-details"]}>
      <div className={styles["coin-details__topbar"]}>
        <Back
          onBeforeGo={onBeforeGo}
          className={styles["coin-details__back"]}
        />
        <Pic
          className={styles["coin-details__pic"]}
          size={PicSize.s}
          src={data?.imageUrl || ""}
        />
        <div className={styles["coin-details__sig"]}>
          <CoinName
            className={styles["coin-details__name"]}
            name={data?.name || ""}
          />
          <Symbol
            className="coin-detailed__symbol"
            name={`(${data?.symbol})` || ""}
          />
          <CoinFav coinId={data?.id} />
        </div>
        <div className="coin-detailed__star"></div>
      </div>
      <div className={styles["coin-details__price-details"]}>
        <CoinPrice
          sign={fc.getCurrencySign(vsCurrency)}
          className={styles["coin-details__current-price"]}
          price={data?.currentPrice || 0}
        />
        <CoinPriceChange
          size={TextSize.ms}
          color={data?.priceChange24h > 0 ? TextColor.green : TextColor.red}
          className={styles["coin-details__price-change"]}
          price={data?.priceChange24h || 0}
        />
        <CoinPriceChangePer
          className="coin-detailed__price-change-per"
          price={data?.priceChangePercentage24h || 0}
          brackets
        />
      </div>
      <CoinDetailedGraph
        coin={data}
        vsCurrency={vsCurrency}
        className={styles["coin-details__graph"]}
      />
      <div className="coin-detailed__transactions">
        <Transaction
          src={data?.imageUrl || ""}
          name={data?.name || ""}
          symbol={`00.00 ${data?.symbol || ""}`}
          className="coin-detailed__transaction"
        />
      </div>
    </div>
  );
};

export default memo(CoinDetailsView);
