import { CoreScaleOptions, Scale } from "chart.js";
import { Context } from "chartjs-plugin-datalabels";

import { CoinDetailsGraphChartContext } from "../util/types";

const options = {
  responsive: true,
  maintainAspectRatio: false,
  scales: {
    y: {
      display: false,
    },
    x: {
      ticks: {
        callback: function (
          this: Scale<CoreScaleOptions>,
          tickValue: string | number,
          index: number
        ) {
          if (typeof tickValue === "number") {
            return index % 2 === 0 ? this.getLabelForValue(tickValue) : null;
          }
          return null;
        },
      },
      grid: {
        display: false,
      },
    },
  },
  elements: {
    point: {
      radius: 0,
    },
  },
  plugins: {
    datalabels: {
      formatter: (value: number, context: Context) => {
        const c = context as CoinDetailsGraphChartContext;
        return c.dataset.max === value || c.dataset.min === value
          ? value
              .toFixed(2)
              .toString()
              .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
          : "";
      },
      clamp: true,
      color: "#6C757D",
      font: {
        family: "sans-serif",
        size: 12,
      },
    },
    zoom: {
      zoom: {
        wheel: {
          enabled: true,
        },
        pinch: {
          enabled: true,
        },
        mode: "xy" as const,
      },
      pan: {
        enabled: true,
        mode: "xy" as const,
      },
    },
  },
};

export type TicksFormatterCb = (
  this: Scale<CoreScaleOptions>,
  tickValue: string | number,
  index: number
) => string | null;

export { options };
