import React, { FC, useEffect, useState } from "react";

import log from "@utils/log";
import { QueryParam } from "@utils/QueryParam";
import classNames from "classnames";
import { useSearchParams } from "react-router-dom";

import Item from "./components/Item";
import { itemsData } from "./config/Data";
import styles from "./ControlsScale.module.scss";
import { ControlsScaleProps } from "./utils/types";

const ControlsScale: FC<ControlsScaleProps> = ({ className }) => {
  // use state
  const [searchParams, setSearchParams] = useSearchParams();
  const [activeValue, setActiveValue] = useState("");

  // Handlers
  const handleClick = (value: string) => {
    const sp = searchParams;
    sp.set(QueryParam.GRAPH_SCALE, value);
    setSearchParams(sp);
  };

  // Utils
  const initSearchParams = () => {
    if (searchParams.get(QueryParam.GRAPH_SCALE) === null) {
      searchParams.set(QueryParam.GRAPH_SCALE, "1 H");
    }
    setSearchParams(searchParams);
  };

  // Lifecycle
  useEffect(() => {
    initSearchParams();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    setActiveValue(`${searchParams.get(QueryParam.GRAPH_SCALE)}`);
  }, [searchParams]);

  // Render
  return (
    <div
      className={classNames(
        className,
        styles["coin-detailed-graph__controls-scale"]
      )}
    >
      {itemsData.map((i) => (
        <Item
          key={i.id}
          value={i.value}
          onClick={handleClick}
          active={activeValue === i.value}
        />
      ))}
    </div>
  );
};

export default ControlsScale;
