import * as React from "react";
import { FC, memo } from "react";

import type { ButtonProps } from "@components/Button/Button";
import ButtonGoPage from "@components/ButtonGoPage";
import { QueryParam } from "@utils/QueryParam";
import classNames from "classnames";

import style from "./ButtonCancel.module.scss";

const ButtonCancel: FC<ButtonProps> = ({ className }) => {
  const handleOnClick = (searchParams: URLSearchParams) => {
    searchParams.delete(QueryParam.NAME);
    searchParams.delete(QueryParam.CATEGORY);
    searchParams.set(QueryParam.PAGE, "1");
  };

  return (
    <ButtonGoPage
      onBeforeGo={handleOnClick}
      className={classNames(style.cancel, className)}
      linked
    >
      Cancel
    </ButtonGoPage>
  );
};

export default memo(ButtonCancel);
