import React, { FC, memo } from "react";

import Text, { TextColor, TextSize, TextWeight } from "@components/Text";
import { QueryParam } from "@utils/QueryParam";
import { SortType } from "@utils/SortType";
import classNames from "classnames";
import { useSearchParams } from "react-router-dom";

import styles from "./TabCoinsOrderItem.module.scss";

type TabCoinsOrderItemProps = {
  className: string;
  id: string;
  sort: SortType;
  active?: boolean;
};

const TabCoinsOrderItem: FC<TabCoinsOrderItemProps> = ({
  id,
  className,
  sort,
  active = false,
}) => {
  // useHooks
  const [searchParams, setSearchParams] = useSearchParams();

  // Handlers
  const handleClick = () => {
    searchParams.set(QueryParam.SORT, sort);
    setSearchParams(searchParams);
  };

  // Render
  return (
    <div
      onClick={handleClick}
      className={classNames(
        className,
        active && styles["tab-coins-order-item_active"]
      )}
    >
      <Text
        size={TextSize.s}
        color={active ? TextColor.blue : TextColor.descend}
        weight={TextWeight.b}
      >
        {id}
      </Text>
    </div>
  );
};

export default memo(TabCoinsOrderItem);
