import React, { FC, memo } from "react";

import { CoinModel } from "@store/models/CoinGecko";
import classNames from "classnames";
import { Sparklines, SparklinesLine } from "react-sparklines";

import styles from "./CoinGraph.module.scss";

export enum LineColor {
  GREEN = "#21BF73",
  RED = "#D90429",
}

export type CoinGraphParams = {
  coin: CoinModel;
  lineColor?: LineColor;
};

const CoinGraph: FC<CoinGraphParams> = ({
  coin,
  lineColor = LineColor.GREEN,
}) => {
  const d = coin.sparklineIn7d.price.slice(-24, -1).map((el: string) => {
    let n = `${el}`.slice(0, 5);
    n = `0.${n}`;
    return parseFloat(n);
  });
  return (
    <div className={classNames(styles["coin-graph"])}>
      <Sparklines data={d} limit={24} width={100} height={50} margin={5}>
        <SparklinesLine color={lineColor} style={{ fill: "none" }} />
      </Sparklines>
    </div>
  );
};

export default memo(CoinGraph);
