import React from "react";

import { CoinName } from "@components/CoinName";
import CoinPrice from "@components/CoinPrice";
import { CoinPriceChangePer } from "@components/CoinPriceChangePer";
import { Pic } from "@components/Pic";
import Symbol from "@components/Symbol";
import classNames from "classnames";

import styles from "./Transaction.module.scss";

export type TransactionProps = {
  src?: string;
  name?: string;
  symbol?: string;
  currentPrice?: number;
  priceChange?: number;
} & React.HTMLAttributes<HTMLDivElement>;

export const Transaction: React.FC<TransactionProps> = React.memo(
  ({
    className,
    name = "",
    src = "",
    symbol = "",
    currentPrice = 0,
    priceChange = 0,
  }) => {
    return (
      <div
        className={classNames(
          className,
          styles.transaction,
          styles.card,
          styles["card_theme_light"]
        )}
      >
        <div className={styles["transaction__col-left"]}>
          <Pic src={src} className={styles.transaction__pic} />
          <div className={styles["transaction__sig"]}>
            <CoinName className={styles["transaction__name"]} name={name} />
            <Symbol
              className={styles["tranasction__symbol"]}
              name={`${symbol}`}
            />
          </div>
        </div>
        <div className={styles["transaction__col-right"]}>
          <CoinPrice
            className={classNames(
              styles["transaction__current-price"],
              styles["transaction__current-price_theme_light"]
            )}
            price={currentPrice}
          />
          <CoinPriceChangePer
            className={classNames(
              styles["transaction__price-change"],
              styles["transaction__price-change_theme_light"]
            )}
            price={priceChange}
          />
        </div>
      </div>
    );
  }
);
