// @TODO: Add validation of schema
export type CoinApi = {
  id: string;
  name: string;
  symbol: string;
  image: string;
  large: string;
  current_price: number;
  price_change_24h: number;
  price_change_percentage_24h: number;
  sparkline_in_7d: {
    price: string[]
  }
};

export type CoinModel = {
  id: string;
  name: string;
  symbol: string;
  imageUrl: string;
  large: string;
  currentPrice: number;
  priceChange24h: number;
  priceChangePercentage24h: number;
  sparklineIn7d: {
    price: string[]
  }
};

export const normalizeCoin = (from: CoinApi): CoinModel => {
  return {
    id: from.id,
    name: from.name,
    symbol: from.symbol,
    imageUrl: from.image,
    large: from.large,
    currentPrice: from.current_price,
    priceChange24h: from.price_change_24h,
    priceChangePercentage24h: from.price_change_percentage_24h,
    sparklineIn7d: from.sparkline_in_7d,
  };
};
