import CurrencySigns from "@utils/CurrencySigns";
import { QueryParam } from "@utils/QueryParam";
import { SortType } from "@utils/SortType";
import setQueryParam from "@utils/useQueryParam";

import { SetStateActionCb } from "./types";

export const cleanBeforeCoinDetails = (sp: URLSearchParams) => {
  sp.delete(QueryParam.CATEGORY);
  sp.delete(QueryParam.NAME);
  // @deprecated
  // sp.delete(QueryParam.PAGE);
  // sp.delete(QueryParam.PER_PAGE);
};

export const initSpPageOnMount = (
  sp: URLSearchParams,
  setSp: SetStateActionCb<string>
) => {
  const p = sp.get(QueryParam.PAGE);
  if (p !== null) {
    setSp(p);
  } else {
    setSp("1");
  }
};

export const initSpVsCurrencyOnMount = (
  sp: URLSearchParams,
  setCoinSign: SetStateActionCb<string>,
  currencySigns: CurrencySigns
) => {
  const cs = sp.get(QueryParam.CURRENCY);
  if (cs !== null) {
    setCoinSign(currencySigns.getCurrencySign(cs));
  } else {
    setCoinSign(currencySigns.getCurrencySign("usd"));
    setQueryParam(sp, QueryParam.CURRENCY, "usd");
  }
};

export const initSpSortOnMount = (sp: URLSearchParams) => {
  const s = sp.get(QueryParam.SORT);
  if (s === null) {
    sp.set(QueryParam.SORT, SortType.MARKET_DESC);
  }
};

export const initSpScrollOnMount = (sp: URLSearchParams) => {
  const s = sp.get(QueryParam.SCROLL);
  if (s === null) {
    sp.set(QueryParam.SCROLL, "0");
  }
};

export const setSpScroll = (sp: URLSearchParams, value: number) => {
  sp.set(QueryParam.SCROLL, value.toString());
};

export const getScrollFromSp = (sp: URLSearchParams): number => {
  const s = sp.get(QueryParam.SCROLL);
  if (s !== null) {
    return parseInt(s);
  }
  return 0;
};
