import CoingeckoStore from "@store/CoinGeckoStore";
import {
  CategoryApi,
  CategoryModel,
  normalizeCategory,
} from "@store/models/CoinGecko";
import { normalizeCollection } from "@store/models/shared/collection";
import { action, makeObservable } from "mobx";

class CategoryStore extends CoingeckoStore<CategoryApi[], CategoryModel> {
  private readonly endpoint = "/coins/categories/list";

  constructor() {
    super();
    makeObservable<CategoryStore>(this, {
      getCoinsCategories: action,
    });
  }

  normalizeApiData(d: CategoryApi[]): void {
    this.setData(
      normalizeCollection(
        d.map((item) => normalizeCategory(item)),
        (el) => el.categoryId
      )
    );
  }

  async getCoinsCategories() {
    this.getDataFromApiStore(this.endpoint);
  }

  destroy(): void {}
}

export default CategoryStore;
