import CoinGeckoStore from "@store/CoinGeckoStore";
import {
  CurrencyApi,
  CurrencyModel,
  normalizeCurrency,
} from "@store/models/CoinGecko/Currency";
import { normalizeCollection } from "@store/models/shared/collection";
import { action, makeObservable } from "mobx";

class CurrencyStore extends CoinGeckoStore<string[], CurrencyModel> {
  private readonly _endpoint = "/simple/supported_vs_currencies";

  constructor() {
    super();

    makeObservable<CurrencyStore>(this, {
      getCurrencies: action,
    });
  }

  async getCurrencies() {
    this.getDataFromApiStore(this._endpoint);
  }

  normalizeApiData(d: string[]): void {
    const m: CurrencyApi[] = d.map((el) => {
      return {
        id: el,
      };
    });
    this.setData(
      normalizeCollection(
        m.map((e) => normalizeCurrency(e)),
        (e) => e.id
      )
    );
  }

  destroy(): void {}
}

export default CurrencyStore;
