interface CurrencySign {
  sign: string;
}

class CurrencySigns {
  readonly signs: Record<string, CurrencySign> = {
    rub: {
      sign: "",
    },
    usd: {
      sign: "$",
    },
    btc: {
      sign: "",
    },
    jpy: {
      sign: "",
    },
  };

  getCurrencySign(id: string) {
    return this.signs[id]?.sign || id.slice(0, 1);
  }
}

export default CurrencySigns;
