import { useQueryParamsStoreInit } from "@hooks/useQueryParamsStoreInit";
import CoinDetailsPage from "@pages/PageCoinDetails";
import MarketOverviewPage from "@pages/PageMarketOverview";
import SearchPage from "@pages/PageSearch";
import SettingsPage from "@pages/PageSettings";
import * as React from "react";
import { Routes, Route } from "react-router-dom";

import styles from "./App.module.scss";

function App() {
  useQueryParamsStoreInit();

  return (
    <div className={styles["App"]}>
      <Routes>
        <Route path="/" element={<MarketOverviewPage />} />
        <Route index element={<MarketOverviewPage />} />
        <Route path={"/search"} element={<SearchPage />} />
        <Route path="/coin">
          <Route path=":id" element={<CoinDetailsPage />} />
        </Route>
        <Route path="/settings" element={<SettingsPage />} />
      </Routes>
    </div>
  );
}

export default App;
