import { MutableRefObject } from "react";

import { SetStateActionCb } from "./types";

export const initRefScrollListWrapper = (
  set: SetStateActionCb<Element | null>,
  ref: MutableRefObject<HTMLDivElement | null>
) => {
  const w = ref.current;
  if (w !== null) set(w.children[0].children[0]);
};

export const setScrollListTop = (list: Element | null, size: number) => {
  if (list !== null) list.scrollTop = size;
};

export const getScrollListTop = (list: Element | null): number => {
  if (list !== null) return list.scrollTop;
  return 0;
};
