import * as React from "react";
import { memo, FC } from "react";

import { ButtonProps } from "@components/Button";
import ButtonGoPage from "@components/ButtonGoPage";
import { Icon } from "@utils/Icon";
import { QueryParam } from "@utils/QueryParam";
import { RoutePath } from "@utils/RoutePath";

export const ButtonGoSearch: FC<ButtonProps> = ({ className }) => {
  // Callbacks
  const handleClick = (params: URLSearchParams) => {
    params.set(QueryParam.PAGE, "1");
  };

  // Render
  return (
    <ButtonGoPage
      className={className}
      icon={Icon.search}
      onBeforeGo={handleClick}
      page={RoutePath.Search}
    />
  );
};

export default memo(ButtonGoSearch);
