import * as React from "react";
import { useCallback, useEffect, useRef, useState } from "react";

import { CoinItem } from "@components/CoinsList/components/CoinItem";
import { Loader } from "@components/Loader";
import { useLocalStore } from "@hooks/useLocalStore";
import CoinStore from "@store/CoinStore";
import { CoinModel } from "@store/models/CoinGecko";
import rootStore from "@store/RootStore";
import CurrencySigns from "@utils/CurrencySigns";
import { QueryParam } from "@utils/QueryParam";
import { ResponseState } from "@utils/ResponseState";
import setQueryParam from "@utils/useQueryParam";
import classNames from "classnames";
import { observer } from "mobx-react-lite";
import InfiniteScroll from "react-infinite-scroll-component";
import { useNavigate, useSearchParams } from "react-router-dom";

import MessageEnd from "./components/MessageEnd";
import {
  getScrollListTop,
  initRefScrollListWrapper,
  setScrollListTop,
} from "./util/dom";
import {
  cleanBeforeCoinDetails,
  getScrollFromSp,
  initSpPageOnMount,
  initSpScrollOnMount,
  initSpSortOnMount,
  initSpVsCurrencyOnMount,
  setSpScroll,
} from "./util/sp";

export type CoinsListProps = {} & React.HTMLAttributes<HTMLDivElement>;

const CoinsList: React.FC<CoinsListProps> = ({ className }) => {
  const currencySigns = new CurrencySigns();
  // use hooks
  const navigate = useNavigate();
  const coinStore = useLocalStore(() => new CoinStore());
  const refScrollWrapper = useRef<HTMLDivElement | null>(null);

  // State
  const [searchParams, setSearchParams] = useSearchParams();
  const [sp, setSp] = useState("1");
  const [coinSign, setCoinSign] = useState("");
  const [category, setCategory] = useState("");
  const [sort, setSort] = useState("");
  const [refList, setRefList] = useState<Element | null>(null);

  // Vars
  const coinsToShow = 7;

  // Handlers
  const handleFetchMore = () => {
    setSp(`${parseInt(sp) + 1}`);
  };
  const handleHasMore = (): boolean => {
    return (
      coinStore.list.length > 0 && coinStore.list.length < coinStore.coinsCount
    );
  };
  const handleClickCoinItem = useCallback(
    (coin: CoinModel) => {
      const sp = searchParams;
      cleanBeforeCoinDetails(sp);
      navigate(`/coin/${coin?.id || ""}?${sp.toString()}`);
    },
    [navigate, searchParams]
  );

  // Utils
  const initSearchParams = () => {
    initSpPageOnMount(searchParams, setSp);
    initSpVsCurrencyOnMount(searchParams, setCoinSign, currencySigns);
    initSpSortOnMount(searchParams);
    initSpScrollOnMount(searchParams);
  };
  const getCategory = useCallback(
    () => searchParams.get(QueryParam.CATEGORY),
    [searchParams]
  );
  const isCategoryChange = useCallback(
    () => getCategory() !== category && getCategory() !== null,
    [category, getCategory]
  );
  const getSort = () => searchParams.get(QueryParam.SORT);
  const isSortChange = () => getSort() !== sort && getSort() !== null;

  // Livesycle
  useEffect(() => {
    initRefScrollListWrapper(setRefList, refScrollWrapper);
    initSearchParams();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    if (refList !== null) {
      setTimeout(() => {
        refList.scrollTop = getScrollFromSp(searchParams);
      }, 500);
    }
  }, [refList]);
  useEffect(() => {
    coinStore.getCoins();
  }, [coinStore]);
  useEffect(() => {
    setSearchParams(setQueryParam(searchParams, QueryParam.PAGE, sp));
    setSearchParams(
      setQueryParam(searchParams, QueryParam.PER_PAGE, coinsToShow.toString())
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sp, setSearchParams]);
  useEffect(() => {
    if (category === "") {
      setCategory(getCategory() || "");
    } else if (isCategoryChange()) {
      setCategory(getCategory() || "");
      setSp("1");
      setScrollListTop(refList, 0);
    }
    if (sort === "") {
      setSort(getSort() || "");
    } else if (isSortChange()) {
      setSort(getSort() || "");
      setSp("1");
      setScrollListTop(refList, 0);
    }
    setCoinSign(
      currencySigns.getCurrencySign(`${searchParams.get(QueryParam.CURRENCY)}`)
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [rootStore.query.params]);

  return (
    <div ref={refScrollWrapper}>
      {coinStore.responseState === ResponseState.LOADING ? (
        <Loader />
      ) : (
        <InfiniteScroll
          next={handleFetchMore}
          loader={<Loader />}
          dataLength={coinStore.list.length}
          className={classNames("coins-list", className)}
          hasMore={handleHasMore()}
          endMessage={coinStore.isListEnd ? <Loader /> : <MessageEnd />}
          height={500}
          onScroll={() => {
            setSpScroll(searchParams, getScrollListTop(refList));
            setSearchParams(searchParams);
          }}
        >
          {coinStore.list.map((coin: CoinModel) => (
            <CoinItem
              onClick={async () => handleClickCoinItem(coin)}
              key={coin?.id}
              coin={coin}
              sign={coinSign}
            />
          ))}
        </InfiniteScroll>
      )}
    </div>
  );
};

export default observer(CoinsList);
