import * as React from "react";
import { FC, useEffect } from "react";

import Loader from "@components/Loader";
import { useLocalStore } from "@hooks/useLocalStore";
import CoinStore from "@store/CoinStore";
import { QueryParam } from "@utils/QueryParam";
import { ResponseState } from "@utils/ResponseState";
import { observer } from "mobx-react-lite";
import { useParams, useSearchParams } from "react-router-dom";

import CoinDetailsView from "./components/CoinDetailsView";

const PageCoinDetails: FC = () => {
  // use hooks
  const { id = "" } = useParams();
  const coinStore = useLocalStore(() => new CoinStore());
  const [searchParams] = useSearchParams();

  // Handlers
  const onBeforeGoBack = (params: URLSearchParams) => {
    params.delete(QueryParam.GRAPH_SCALE);
  };

  // useEffect
  useEffect(() => {
    let vsCurrency = searchParams.get(QueryParam.CURRENCY);
    if (vsCurrency !== null) {
      coinStore.getCoinsByCurrencyId(vsCurrency, id);
    } else {
      coinStore.getCoinsByCurrencyId("usd", id);
    }
  }, [coinStore, id, searchParams]);

  // Render
  return (
    <>
      {coinStore.responseState === ResponseState.LOADING ? (
        <Loader />
      ) : (
        <CoinDetailsView
          onBeforeGo={onBeforeGoBack}
          data={coinStore.list[0]}
          vsCurrency={coinStore.vsCurrency}
        />
      )}
    </>
  );
};

export default observer(PageCoinDetails);
