import * as React from "react";

import Text, { TextProps, TextSize } from "@components/Text";
import classNames from "classnames";

import styles from "./CoinPriceChange.module.scss";

export type CoinPriceChangeProps = {
  price: number;
} & TextProps;

export const CoinPriceChange: React.FC<CoinPriceChangeProps> = ({
  price,
  className,
  size = TextSize.st,
  color,
  ...htmlSpanAttrs
}) => {
  return (
    <Text
      size={size}
      className={classNames(styles["coin-price-change"], className)}
      color={color}
      {...htmlSpanAttrs}
    >
      {price.toFixed(2)}
    </Text>
  );
};
