import * as React from "react";
import { ChangeEvent, FC, memo, useCallback, useEffect, useState } from "react";

import { QueryParam } from "@utils/QueryParam";
import setQueryParam from "@utils/useQueryParam";
import classNames from "classnames";
import { useSearchParams } from "react-router-dom";

import styles from "./SearchByNameInput.module.scss";

type SearchByNameInputProps = React.InputHTMLAttributes<HTMLInputElement> & {};

const SearchByNameInput: FC<SearchByNameInputProps> = ({
  className,
  placeholder = "Search Cryptoconcurency",
  ...args
}) => {
  // State
  const [searchParams, setSearchParams] = useSearchParams();
  const [sp, setSp] = useState("");

  // Handlers
  const handleInputChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    setSp(e.target.value);
  }, []);

  // Hooks
  useEffect(() => {
    const n = searchParams.get(QueryParam.NAME);
    if (n !== null) {
      setSp(n);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    if (searchParams.get(QueryParam.CATEGORY) !== null) {
      setSearchParams(setQueryParam(searchParams, QueryParam.CATEGORY, ""));
    }
    setSearchParams(setQueryParam(searchParams, QueryParam.NAME, sp));
    setSearchParams(setQueryParam(searchParams, QueryParam.PAGE, "1"));
  }, [sp, setSearchParams]);

  return (
    <div className={styles["search-by-name-input"]}>
      <input
        value={sp}
        className={classNames(className, styles["search-by-name-input__input"])}
        type="text"
        onChange={handleInputChange}
        placeholder={placeholder}
        {...args}
      />
    </div>
  );
};

export type { SearchByNameInputProps };
export default memo(SearchByNameInput);
