import * as React from "react";
import { FC, memo } from "react";

import Text, { TextSize } from "@components/Text";

export type CoinNameProps = {
  name: string;
  className?: string;
};

export const CoinName: FC<CoinNameProps> = memo(({ name, className }) => {
  return (
    <Text size={TextSize.ms} className={className}>
      {name}
    </Text>
  );
});
