import React, { FC } from "react";

import Text, { TextColor, TextSize } from "@components/Text";
import classNames from "classnames";

import styles from "./Item.module.scss";

export type ItemProps = {
  className?: string;
  value?: string;
  onClick: (val: string) => void;
  active?: boolean;
};

const Item: FC<ItemProps> = ({
  className,
  value = "",
  active = false,
  onClick,
}) => {
  return (
    <div
      onClick={() => onClick(value)}
      className={classNames(
        className,
        styles["controls-scale__item"],
        active && styles["controls-scale__item_active"]
      )}
    >
      <Text
        size={TextSize.es}
        color={active ? TextColor.blue : TextColor.descend}
      >
        {value}
      </Text>
    </div>
  );
};

export default Item;
